@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Poppins:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1 {
    text-align: center;
    text-transform: capitalize;
    font-size: 44px;
    padding: 30px 0;
    margin: 0;
}

.card-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 2rem;
}

.search-form {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-input {
    border: 1px solid #eee;
    border-radius: 10px;
    padding: 15px 30px;
}

.country {
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, .3);
    background-image: linear-gradient(135deg, #5968da, #001064);
    text-align: center;
    color: #fff !important;
}

@media screen and (max-width: 576px) {
    .card-list {
        grid-template-columns: 1fr;
    }
}

.country h5 {
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: 500;
    white-space: normal;
}
